import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home.vue')
  },
  {
    path: '/main',
    name: 'Main',
    component: () => import('../views/Main.vue')
  },
  {
    path: '/employees',
    name: 'Employees',
    component: () => import('../views/Employees.vue')
  },
  {
    path: '/visitors',
    name: 'Visitors',
    component: () => import('../views/Visitors.vue')
  },
  {
    path: '/tpc',
    name: 'TPC',
    component: () => import('../views/TPC.vue')
  },
  {
    path: '/infirmary',
    name: 'Infirmary',
    component: () => import('../views/Infirmary.vue')
  },
  {
    path: '/analytics',
    name: 'Analytics',
    component: () => import('../views/Analytics.vue')
  },
  {
    path: '/monthly-reports',
    name: 'MonthlyReport',
    component: () => import('../components/analytics/MonthlyReport.vue')
  },
  {
    path: '/classification-reports',
    name: 'ClassificationReport',
    component: () => import('../components/analytics/EmployeeClassificationReport.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
