    async getAllTriageEREmployees(state) {

      state.commit("setFormLoading", true);

      const response = await fetch(
        `${this.state.apiUrl}etriage/dashboard?auth=${this.state.apiKey}&isForCovidER=1`,
        {
          method: "GET",
          headers: { "Content-Type": "application/json" },
        }
      ).then((response) => response.json());

      for (var result of response) {
        if (result.symptomsAndHistory != null) {
          let symptoms = result.symptomsAndHistory
            .replace(/[_]/g, " ")
            .toUpperCase()
            .split(";")
            .join("\n");
          result.symptomsAndHistory = `\n${symptoms}\n`;
        } else {
          result.symptomsAndHistory = "N/A";
        }

        const d = new Date(result.date)
        const ye = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(d)
        const mo = new Intl.DateTimeFormat('en', { month: 'long' }).format(d)
        const da = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(d)
        const formattedDate = `${mo} ${da}, ${ye}`;
        result.date = formattedDate;
      }

      state.commit("getAllTriageEREmployees", response);

      state.commit("setFormLoading", false);
    },

    async getDTRWithoutTriage(state) {
      state.commit("setDTRFormLoading", true);
      const response = await fetch(
        `${this.state.apiUrl}etriage/timedata-no-triage?auth=${this.state.apiKey}`,
        {
          method: "GET",
          headers: { "Content-Type": "application/json" },
        }
      );

      const responseJson = await response.json();
      for (var result of responseJson) {
        const d = new Date(result.date)
        const ye = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(d)
        const mo = new Intl.DateTimeFormat('en', { month: 'long' }).format(d)
        const da = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(d)
        const formattedDate = `${mo} ${da}, ${ye}`;
        result.date = formattedDate;
      }
      if (responseJson.length > 0) {
        this.showStatus = true;
      }

      state.commit("geDTREmployeesWithoutTriage", responseJson);
      state.commit("setDTRFormLoading", false);
    },
    async checkAccessRightsForClearing(state, code) {
      state.commit("setFormLoading", true);
      const access = {
        systemName: 'ETriage Dashboard',
        moduleName: 'Infirmary Clearing',
      }

      const responseAccess = await fetch(
        `${this.state.apiUrl}auth/access?auth=${this.state.apiKey}&code=${code}&systemName=${access.systemName}&moduleName=${access.moduleName}`,
        {
          method: "GET",
          headers: { "Content-Type": "application/json" },
        }
      ).then((responseAccess) => responseAccess.json());

      if(!responseAccess.access) {
        this.isFormLoading = false;
        state.commit("setAccessRightsForClearing", false);
        state.commit("setFormMessage", `You have no access rights to UERM ${access.systemName}: ${access.moduleName}`);
        return;
      }

      state.commit("setAccessRightsForClearing", true);
      state.commit("setFormLoading", false);
    },
    async logoutEmployee(state) {
      localStorage.clear();
      state.commit("logout");
      state.commit("setFormMessage", null);
      state.commit("checkLogin", false);
      state.commit("checkInfirmary", false);
    },
