<template>
  <div>
    <v-app-bar
        app
        color="primary"
        dark
      >
      <div class="d-flex align-center">
        <v-btn outlined @click.stop="drawer = !drawer" small>E-Triage Dashboard</v-btn>
      </div>
      <v-spacer></v-spacer>
      <div class="d-flex align-center">
        <button outlined v-if="notificationsSupported" @click="toggleSubscription" :disabled="buttonDisabled"><v-icon>fa-bell</v-icon></button>
        <!-- <a class="pl-3"  href="#" @click="toInfirmary" v-if="this.isInfirmary && this.isLoggedIn"><v-icon class="mx-2" >fas fa-hospital-user</v-icon></a> -->
        <a href="#" @click="logout" v-if="this.isLoggedIn"><v-icon class="mx-2" >fas fa-sign-out-alt</v-icon></a>
      </div>
      
    </v-app-bar>
  
    <v-navigation-drawer
      v-model="drawer"
      fixed
      temporary
      v-if="isLoggedIn"
      color="primary"
      dark
    >
      <v-list dense>
        <v-list-item>
          <v-btn outlined @click.stop="drawer = !drawer" small>E-Triage Dashboard</v-btn>
        </v-list-item>
        <v-list-item
          v-for="item in items"
          :key="item.title"
          link
          :to="item.route"
        >
          <v-list-item-icon style="width:24px">
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
// import serviceWorkerJS from '../../service-worker.js';
export default {
  data() {
    return {
      drawer: null,
      items: [
        { title: 'DASHBOARD', icon: 'fa fa-home', route: '/main' },
        { title: 'INFIRMARY', icon: 'fa fa-hospital-user', route: '/infirmary' },
        { title: 'ANALYTICS', icon: 'fa fa-chart-line', route: '/analytics' },
      ],
      notificationsSupported: true,
      notificationsEnabled: false,
      serviceWorkerRegistation: null,
      buttonDisabled: false,
      showMessage: true,
      refreshing: false,
      registration: null,
      updateExists: false,
    }
  },
  // created() {
  //   document.addEventListener('swUpdated', this.showRefreshUI, {once: true})
  //   navigator.serviceWorker.addEventListener('controllerchange', () => {
  //       if (this.refreshing) return
  //       this.refreshing = true
  //       // Here the actual reload of the page occurs
  //       window.location.reload()
  //   })
  // },
  computed: {
    ...mapGetters(["isLoggedIn","isInfirmary","userDetails"]),
  },
  methods: {
    hideMessage() {
        this.showMessage = false
    },
    showRefreshUI(e) {
        this.registration = e.detail
        this.updateExists = true  
    },
    refreshApp() {
        this.updateExists = false
        if (!this.registration || !this.registration.waiting) return
        // send message to SW to skip the waiting and activate the new SW
        this.registration.waiting.postMessage('skipWaiting')
    },
    async logout(){
      await this.$store.dispatch("logoutEmployee");
      this.$router.push("/");
    },
    async toInfirmary() {
      this.$router.push("/infirmary");
    },
    async toggleSubscription() {
      console.log(this.notificationsSupported)
      // await this.$store.dispatch("subscribeUser");
      if (this.notificationsSupported) {
        console.log('notified')
        console.log(this.notificationsEnabled)
        this.buttonDisabled = true
        // Find out if we need to create a subscription or delete it
        if (!this.notificationsEnabled) {
            // Ask permission and when granted, create new subscription
            Notification.requestPermission()
            .then(result => {
                console.log(result)
                // if granted, create new subscription
                if (result === 'granted') {
                    this.createSubscription()
                    .then(sub => {
                        console.log('subscription created on the client', sub);
                        this.subscription = sub

                        const user = {
                          username: localStorage.getItem('username')
                        }
                        
                        const subscribeUser = fetch("http://localhost:3000/etriage/subscription", {
                          method: "POST",
                          body: JSON.stringify(user),
                          headers: {
                            "content-type": "application/json"
                          }
                        });

                        return subscribeUser;

                        // get new (or existing user) from backend
                        // return axios.post(`${process.env.VUE_APP_API_PATH}/user`, )

                        // return await fetch("http://localhost:3000/etriage/subscription&user=1", {
                        //   method: "POST",
                        //   body: JSON.stringify(subscription),
                        //   headers: {
                        //     "content-type": "application/json"
                        //   }
                        // });
                    })
                    .then(({data}) => {
                        const { user } = data
                        console.log('user created on the server', user);
                        localStorage.setItem('username', user.name)

                        const subs = {
                          subscription: this.subscription,
                          userId: user.id,
                        }
                        // store new subscription on the server
                        const response = fetch("http://localhost:3000/etriage/subscription", {
                          method: "POST",
                          body: JSON.stringify(subs),
                          headers: {
                            "content-type": "application/json"
                          }
                        });

                        return response;

                        // return axios.post(`http://localhost:3000/etriage/subscription`, {
                        //     subscription: this.subscription,
                        //     userId: user.id,
                        // })
                    })
                    .then(() => {
                        this.showNotification()
                        this.buttonDisabled = false
                        this.notificationsEnabled = true
                    })
                } else {
                    console.log('User did not granted permission')
                }
            })
        } else {
            // Destroy subscription
            console.log('Disable subscription');
            if (this.subscription !== null) {
                // destroy on the server
                // return axios.post(`${process.env.VUE_APP_API_PATH}/subscription/delete`, {
                //     endpoint: this.subscription.endpoint,
                // })
                // // unsubscribe on the client
                // .then(() => this.subscription.unsubscribe())
                // .then(() => {
                //     // update the data
                //     this.notificationsEnabled = false
                //     this.buttonDisabled = false
                //     this.subscription = null
                // })
            }
        }
      }
    },

    async createSubscription() {
      console.log('ask for active service worker registration');
      if (this.serviceWorkerRegistation === null) {
        console.log('herererere')
        console.log(navigator.serviceWorker.ready)
        navigator.serviceWorker.ready
        .then(swreg => {
          this.serviceWorkerRegistation = swreg
          console.log('A service worker is active:', swreg.active);
          
          return this.subscribe(this.serviceWorkerRegistation)
          // At this point, you can call methods that require an active
          // service worker, like registration.pushManager.subscribe()
        });

        // const subscription = await register.pushManager.subscribe({
        //   userVisibleOnly: true,
        //   applicationServerKey: urlBase64ToUint8Array(publicVapidKey)
        // });
        // const register = await navigator.serviceWorker.register(serviceWorkerJS, {
        //   scope: "/"
        // });
        // console.log(navigator.serviceWorker, register)
        //   return navigator.serviceWorker.ready // returns a Promise, the active SW registration
        //   .then(swreg => {
        //       this.serviceWorkerRegistation = swreg
        //       console.log(swreg)
        //       return this.subscribe(this.serviceWorkerRegistation)
        //   })
      } else {
          return this.subscribe(this.serviceWorkerRegistation)
      }
    },
    getSubscription(swreg) {
        console.log('ask for available subscription');
        return swreg.pushManager.getSubscription()
    },
    subscribe(swreg) {
        console.log('create new subscription for this browser on this device');
        console.log('pasokditodin')
        // create new subscription for this browser on this device
        const vapidPublicKey = 'BHebmqRIcMFi18L5VUNUXVxT3E5gcbxSsL7LPMPhAMnZslSlMilnJX7rAE-0ufoLY2lF6QQ1SERXPckKIw3gbng'
        const convertedVapidPublicKey = this.urlBase64ToUint8Array(vapidPublicKey)
        // return the subscription promise, we chain another then where we can send it to the server
        return swreg.pushManager.subscribe({
            userVisibleOnly: true,
            // This is for security. On the backend, we need to do something with the VAPID_PRIVATE_KEY
            // that you can find in .env to make this work in the end
            applicationServerKey: convertedVapidPublicKey
        })
    },
    showNotification() {
        this.serviceWorkerRegistation.showNotification('Notifications granted', {
            body: 'Here is a first notification',
            icon: '/img/icons/android-chrome-192x192.png',
            image: '/img/autumn-forest.png',
            vibrate: [300, 200, 300],
            badge: '/img/icons/plint-badge-96x96.png',
        })
    },
    findSubscription() {
        console.log('get active service worker registration');
        return navigator.serviceWorker.ready
        .then(swreg => {
            console.log('haal active subscription op');
            this.serviceWorkerRegistation = swreg
            return this.getSubscription(this.serviceWorkerRegistation)
        })
    },
    createPushNotification() {
        // get the textarea content and send it to the server.
        // The server will create a notifications that will be pushed
        // return axios.post(`${process.env.VUE_APP_API_PATH}/notify`, {
        //     username: localStorage.getItem('username'),
        //     message: this.message,
        // })
        // .then(response => {
        //     this.message = null
        // })
    },
    urlBase64ToUint8Array(base64String) {
        const padding = '='.repeat((4 - base64String.length % 4) % 4);
        const base64 = (base64String + padding)
            .replace(/-/g, '+')
            .replace(/_/g, '/');

        const rawData = window.atob(base64);
        let outputArray = new Uint8Array(rawData.length);

        for (let i = 0; i < rawData.length; ++i) {
            outputArray[i] = rawData.charCodeAt(i);
        }
        return outputArray;
    },
  },
};
</script>