<template>
  <v-bottom-navigation fixed
    app
    class="primary"
    horizontal
    dark
  >
    <v-btn v-if="isLoggedIn">
      <span>{{userDetails.userName}}</span>
      <v-icon>fa fa-user</v-icon>
    </v-btn>

    <v-btn v-if="!isLoggedIn">
      <span  class="text--white">UERM ETRIAGE DASHBOARD</span>
      <v-icon>fa fa-hospital</v-icon>
    </v-btn>

  </v-bottom-navigation>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  created() {
    this.checkUserDetails();
  },
  computed: {
    ...mapGetters(["isLoggedIn","isInfirmary","userDetails"]),
  },
  methods: {
    async checkUserDetails() {
      await this.$store.dispatch("checkUserDetails");
    },
  }
};
</script>