import Vue from 'vue';
import Vuetify from 'vuetify/lib';
import "@fortawesome/fontawesome-free/css/all.css";

Vue.use(Vuetify);

export default new Vuetify({
  icons: {
    iconfont: "fa", // default - only for display purposes
  },
  theme: {
    themes: {
      light: {
        primary: '#1976d2',
        secondary: '#e91e63',
        accent: '#03a9f4',
        error: '#f44336',
        warning: '#ff9800',
        info: '#00bcd4',
        success: '#4caf50',
      },
    },
  },
});
